.card {
  background: #fff;
  border-radius: 2px;
  height: 185px;
  margin: 1rem;
  position: relative;
  width: 225px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 60%
}


.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.green {
  color: green;
  font-size: 1.2em
}
.red{
  color: red;
  font-size: 1.2em
}

#header{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  background-color: purple;
  color: white;
  font-size: 1.6em;
  padding: 30px;
}

#friend-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

